@media(max-width: 1210px){
    .why__img {
        margin-right: 115px;
        width: 650px; 
    }
    .why__preheading {
        font-size: 42px;
        margin-bottom: 30px;
    }
    .statistic{
        margin-bottom: 140px;
    }
    .course__title, .benefits__title, .lesson__title, .certificate__heading, .our-team__text, .subscribe__heading{
        font-size: 42px;
    }
    .course, .lesson{
        margin-bottom: 150px;
    }
    .benefits__btn--com::before, .benefits__btn--exp::before, .benefits__btn--feed::before, .benefits__btn--lib::before{
        left: 50px;
    }
    .benefits__btn{
        padding: 10px 52px 10px 94px;
    }
}

@media(max-width: 1160px){
    .statistic{
        margin-bottom: 90px;
    }
    .statistic__text::after{
        display: none;
    }
    .top{
        padding-top: 110px;
        padding-bottom: 170px;
    }
    .top__content-heading{
        font-size: 58px;
        margin-bottom: 30px;
    }
    .top__img{
        width: 550px;
        height: 525px;
    }
    .top__content{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .why__img {
        margin-right: 80px;
        width: 50%;
    }
    .why__preheading {
        margin-bottom: 25px;
    }
    .why {
        margin-bottom: 130px;
    }
    .course__title, .benefits__title, .lesson__title, .certificate__heading, .our-team__text, .subscribe__heading{
        font-size: 38px;
    }
    .course, .lesson{
        margin-bottom: 130px;
    }
    .benefits__btn--com::before, .benefits__btn--exp::before, .benefits__btn--feed::before, .benefits__btn--lib::before{
        display: none;
    }
    .benefits__btn{
        width: 25%;
        text-align: center;
        padding: 10px 0;
    }
    .benefits {
        margin-bottom: 100px;
    }
    .certificate__inner{
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
    .certificate__img{
        max-width: 90%;
    }
    .certificate__content{
        margin-bottom: 60px;
    }
    .certificate__heading{
        margin-bottom: 20px;
    }
    .certificate__logo{
        margin-right: 15px;
    }
    .certificate__logo:last-child{
        margin-right: 0;
    }
}

//1160px

@media(max-width: 1024px){
    .cards {
        display: grid;
        grid-template-columns: repeat(1,1fr);
    }
    .card{
        width: 90%;
    }
    .footer__nav{
        display: none;
    }
}

@media (max-width: 1001px){
    .statistic::after{
        display: none;
    }
    .top{
        padding-top: 80px;
        padding-bottom: 150px;
    }
    .btn.top__content-courses {
        width: 185px;
    }
    .top__content-about{
        width: 130px !important;
    }
    .top__content-heading{
        font-size: 54px;
    }
    .top__img {
        width: 450px;
        height: 460px;
    }
    .mobile-nav-button {
        display: flex !important;
    }
    .menu{
        display: none;
    }
    .mobile-nav__logo{
        margin-bottom: 30px;
        width: 180px;
    }
    .mobile-nav__item{
        margin-bottom: 30px;
        font-weight: 700;
        font-size: 35px;
        text-align: center;
    }
    .why {
        margin-bottom: 120px;
    }
    .benefits__card{
        flex-direction: column;
    }
    .benefits__card-heading{
        text-align: center;
    }
    .benefits__card-content{
        margin-bottom: 30px;
    }
    .benefits__card-img{
        max-width: 100%;
        z-index: 3;
    }
    .lesson{
        margin-bottom: 90px;
    }
    .our-team{
        padding-top: 120px;
    }
}


@media(max-width: 900px){
    .top{
        padding-top: 90px;
        padding-bottom: 100px;
    }
    .top__img {
        width: 350px;
        height: 380px;
    }
    .top__content-heading {
        font-size: 48px;
    }
    .top__content {
        width: 350px;
    }
    .statistic{
        padding-bottom: 40px;
    }
    .statistic__items{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        justify-content: center;
    }
    .statistic::before, .top__content-btns::after{
        display: none;
    }
    .statistic__item {
        justify-content: center;
        margin-bottom: 30px;
    }
    .statistic__heading{
        margin-right: 30px;
    }
    .course{
        margin-bottom: 120px;
    }
    .lesson{
        margin-bottom: 90px;
        }
    .our-team__card-text{
        font-size: 14px;
    }
    .our-team__card-heading{
        font-size: 17px;
    }
    .subscribe::before{
        left: -70px;
    }
    .subscribe::after{
        right: -70px;
    }
}

@media(max-width: 801px){
    .footer__sign{
        display: none;
    }
}

@media(max-width: 768px){
    .top{
        padding-top: 70px;
        padding-bottom: 70px;
    }
    .big__btn{
        display: none;
    }
    .top__content {
        width: 400px;
    }
    .top__content-heading {
        font-size: 54px;
    }
    .top__img {
        display: none;
    }
    .top__inner{
        justify-content: center;
    }
    .why__inner {
        flex-direction: column;
        align-items: center;
    }
    .why__img {
        width: 95%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 40px;
        min-height: 350px;
    }
    .why__preheading {
        margin-bottom: 20px;
    }
    .why {
        margin-bottom: 100px;
    }
    .course__top {
        flex-direction: column;
        align-items: flex-start;
    }
    .course__title {
        margin-bottom: 20px;
    }
    .course__title, .benefits__title, .lesson__title, .certificate__heading, .our-team__text, .subscribe__heading, .why__preheading{
        font-size: 34px;
    }
    .card__content {
        justify-content: center;
        margin-top: 0;
    }
    .card{
        margin-right: auto;
        margin-left: auto;
        height: 180px;
        &__img{
            height: 179px;
        }
    }
    .card__text {
        font-size: 17px;
        margin: 10px 0;
    }
    .course{
        margin-bottom: 100px;
    }
    .benefits__buttons{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        justify-content: center;
        margin-bottom: 85px;
    }
    .benefits__btn{
        width: 100%;
    }
    .benefits__title{
        margin-bottom: 30px;
    }
    .benefits {
        margin-bottom: 80px;
    }
    .lesson::after{
        display: none;
    }
    .lesson__event{
        flex-direction: column;
    }
    .lesson__event-btn{
        position: static;
    }
    .lesson__event-num, .lesson__event-time, .lesson__event-info{
        margin: 0 0 17px;
        text-align: center;
    }
    .lesson::before{
        display: none;
    }
    .lesson__view{
        justify-content: space-around;
    }
    .lesson__text{
    font-size: 25px;
    }
    .lesson, .certificate, .our-team{
        margin-bottom: 70px;
    }
    .certificate__content{
        margin-bottom: 40px;
    }
    .certificate__img{
        max-width: 100%;
    }
    .our-team{
        padding: 100px 0 100px;
    }
    .subscribe::before{
        display: none;
    }
    .subscribe::after{
        display: none;
    }
    .subscribe{
        padding: 100px 0 100px;
    }
    .subscribe__input{
        width: 350px;
    }
}

//768px


@media(max-width: 601px){
    .statistic__items{
        grid-template-columns: repeat(1,1fr);
    } 
    .statistic__item{
        font-size: 40px;
        justify-content: flex-start;
        margin-left: 50px;
    }
    .statistic {
        margin-bottom: 60px;
    }
    .why {
        margin-bottom: 90px;
    }
    .lesson__view{
        align-items: center;
        flex-direction: column;
    }
    .lesson__text{
        margin-right: 0;
        margin-bottom: 15px;
    }
    .subscribe__form{
        flex-direction: column;
    }
    .subscribe__input{
        margin-right: 0;
        margin-bottom: 15px;
        width: 90%;
    }
    .subscribe__heading{
        margin-bottom: 20px;
        font-size: 25px;
    }
    .subscribe{
        padding: 80px 0 80px;
    }
}

@media(max-width: 501px){
    .card__img {
        height: auto;
        min-width: 250px;
        width: 70%;
        margin: auto;
        min-height: 150px;
        margin-bottom: 25px;
    }
    .card{
        margin-right: auto;
        margin-left: auto;
        flex-direction: column;
        height: auto;
    }
    .card__content{
        width: 80%;
        margin-right: auto;
        margin-left: auto;
        margin-bottom: 25px;
    }
    .course{
        margin-bottom: 80px;
    }
    .lesson{
    margin-bottom: 70px;
    }
    .slick-slide img{
        width: 100%;
    }
    .footer__content{
        flex-direction: column;
        align-items: center;
    }
    .footer__text{
        margin-bottom: 20px;
    }
    .footer__info{
        margin-bottom: 20px;
    }
    .footer__content{
        padding: 60px 0;
    }
}

@media(max-width: 426px){
    .top{
        padding-bottom: 60px;
    }
    .top__content-courses {
        width: 160px;
    }
    .top__content-about {
        width: 110px;
    }
    .top__content-img{
        width: 60px;
    }
    .header__login-link{
        display: none;
    }
    .header__login-link--login::before{
        display: none;
    }
    .top::after{
        display: none;
    }
    .top__content-heading {
        font-size: 39px;
    }
    .top__content {
        width: 290px;
    }
    .why__preheading {
        font-size: 30px;
        margin-bottom: 15px;
    }
    .why {
        margin-bottom: 80px;
    }
    .course__title, .benefits__title, .lesson__title, .certificate__heading, .our-team__text, .subscribe__heading{
        font-size: 30px;
    }
    .course{
        margin-bottom: 60px;
    }
    .benefits__buttons{
        grid-template-columns: repeat(1,1fr);
        margin-bottom: 170px;
    }
    .benefits__btn{
        width: 80%;
        margin: 0 auto;
    }
    .benefits__title{
        margin-bottom: 15px;
    }
    .benefits {
        margin-bottom: 60px;
    }
    .our-team{
        padding-top: 70px;
        padding-bottom: 70px;
    }
    .footer__copy-content{
        flex-direction: column;
    }
    .footer__copy-information{
        margin-bottom: 10px;
    }
}

