$font-color: #1E212C;
$heading-color: #424551;
%font{
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    color: $font-color;
}
%bigfont{
    font-weight: 900;
    line-height: 130%;
    color: $font-color;
}


@function max($numbers...) {
 @return m#{a}x(#{$numbers});
}
@function min($numbers...) {
 @return m#{i}n(#{$numbers});
}