.top{
    position: relative;
    padding-bottom: 190px;
    padding-top: 128px;
    &::after{
        position: absolute;
        content: '';
        width: 110px;
        height: 110px;
        background-image: url(../img/index/shapes.png);
        top: -68px;
        right: 96px;
    }
    &__inner{
        display: flex;
        justify-content: space-between;
    }
    &__content{
        
        &-playvideo{
            position: relative;
            display: flex;
            align-items: center;
            margin-bottom: 24px;
            &::after{
                position: absolute;
                content: '';
                width: 96px;
                height: 96px;
                background-image: url(../img/index/arrows.svg);
                top: -65px;
                left: -151px;
                background-repeat: no-repeat;
            }
        }
        &-heading{
            font-weight: 900;
            font-size: 64px;
            line-height: 130%;
            letter-spacing: 1px;
            color: $font-color;
            max-width: 460px;
            margin-bottom: 60px;
            position: relative;
            &::after{
                position: absolute;
                content: '';
                width: 88px;
                height: 47px;
                background-image: url(../img/index/shape.png);
                bottom: 40px;
                right: -75px;
                background-repeat: no-repeat;
            }
        }
        
        &-about{
            height: 52px;
            width: 148px;
            background-color: inherit;
            border-radius: 4px;
            border: 1px solid #FF3F3A;
            margin-right: 24px;
        }
        &-courses{
            width: 201px;
            height: 52px;
        }
        &-btns{
            position: relative;
            display: flex;
            &::after{
                position: absolute;
                content: '';
                width: 61px;
                height: 61px;
                background-image: url(../img/index/back.svg);
                bottom: -140px;
                right: 0;
                background-repeat: no-repeat;
            }
        }
}
}


.statistic{
    position: relative;
    padding-bottom: 80px;
    margin-bottom: 180px;
    &::after{
        position: absolute;
        content: '';
        width: 172px;
        height: 172px;
        background-image: url(../img/index/circles.png);
        top: 0;
    }
    &::before{
        position: absolute;
        content: '';
        width: 155px;
        height: 137px;
        background-image: url(../img/index/dots.svg);
        top: -180px;
        right: 0;
        background-repeat: no-repeat;
    }
    &__items{
        display: flex;
        justify-content: space-between;
        max-width: 1158px;
    }
    &__item{
        display: flex;
        align-items: center;
    }
    &__heading{
        font-weight: 900;
        font-size: 46px;
        line-height: 130%;
        color: $font-color;
        margin-right: 12px;
    }
    &__text{
        position: relative;
        font-size: 16px;
        line-height: 160%;
        color: $font-color;
        &::after{
            content: '';
            position: absolute;
            width: 8px;
            height: 8px;
            background: #FF3F3A;
            border-radius: 90px;
            right: -66px;
            top: 35%;
        }
        
    }
}

.header--index, .top, .statistic{
    background: linear-gradient(55.95deg, rgba(255, 63, 58, 0.2) 0%, rgba(247, 94, 5, 0.2) 100%); 
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    background-attachment: fixed;

}


.why{
    position: relative;
    margin-bottom: 180px;
    &__inner{
        display: flex;
    }
    &__img{
        margin-right: 135px;
        border-radius: 4px;
        object-fit: cover;
    }
    &__heading{
        color: $font-color;
        font-weight: 700;
        font-size: 16px;
        line-height: 150%;
        letter-spacing: 1px;
        margin-bottom: 8px;
    }
    &__preheading{
        color: $font-color;
        font-weight: 900;
        font-size: 46px;
        line-height: 130%;
        margin-bottom: 40px;
    }
    &__list{
        font-weight: 400;
        font-size: 16px;
        line-height: 160%;
    }
    &__item{
        position: relative;
        margin-bottom: 12px;
        padding-left: 32px;
        &:last-child{
            margin-bottom: 22px;
        }
        &::before{
            position: absolute;
            content: url(../img/index/check.png);
            left: 0;
            top: 3px;
        }
    }
    &__btn{
        width: 160px;
        height: 44px;
        color:#fff;
        letter-spacing: 0.5px;
        font-weight: 700;
        font-size: 14px;
    }
}

.course{
    margin-bottom: 150px;
    &__top{
        display: flex;
        justify-content: space-between;
        margin-bottom: 60px;
        align-items: flex-end;
    }
    &__subtitle{
        font-weight: 700;
        font-size: 16px;
        color: $font-color;
        line-height: 150%;
        margin-bottom: 8px;
    }
    &__title{
        font-weight: 900;
        font-size: 46px;
        line-height: 130%;
        color: $font-color;
    }
    &__btn{
        height: 52px;
        width: 202px;
        border-radius: 4px;
        border: 1px solid #F75E05;
        background-color: inherit;
        background: linear-gradient(55.95deg, #FF3F3A 0%, #F75E05 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
}

.cards{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
}
.card{
    width: 95%;
    box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.16), 0px 30px 24px -10px rgba(154, 156, 165, 0.1), 0px 12px 10px -6px rgba(154, 156, 165, 0.08), 0px 4px 4px -4px rgba(30, 33, 44, 0.05);
    border-radius: 4px;
    border: 1px solid #E5E8ED;
    margin-bottom: 30px;
    height: 216px;
    display: flex;
    &__img{
        margin-right: 32px;
        max-width: none;
    }
    &__content{
        display: flex;
        flex-direction: column;
        margin-top: 33px;
        max-width: 320px;
    }
    &__btn{
        color: #FFFFFF;
        border-radius: 4px;
        padding: 1px 8px;
    }
    &__text{
        font-weight: 700;
        font-size: 20px;
        line-height: 150%;
        color: $font-color;
        margin: 20px 0;
    }
    &__cost{
        color: #FF4242;
        font-size: 18px;
        font-weight: 700;
    }
    &__author{
        color: #787A80;
        font-weight: 400;
        font-size: 18;
    }
}

.marketing{
    background: #03CEA4;
}
.hr{
    background: #F89828;
}
.management{
    background: #5A87FC;
}
.design{
    background: #F52F6E;
}

.benefits{
    margin-bottom: 120px;
    &__subtitle, &__title{
        text-align: center;
        color: $font-color;
    }
    &__title{
        font-weight: 900;
        font-size: 46px;
        line-height: 130%;
        margin-bottom: 60px;
    }
    &__subtitle{
        letter-spacing: 1px;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 16px;
    }
    &__buttons{
        display: flex;
        justify-content: space-between;
        height: 46px;
        cursor: pointer;
        color: #9A9CA5;
        font-weight: 700;
        margin-bottom: 70px;
    }
    &__btn{
        padding: 10px 72px 10px 94px;
        position: relative;
        &--exp::before, &--feed::before, &--com::before, &--lib::before{
            position: absolute;
            content: '';
            width: 16px;
            height: 16px;
            left: 70px;
            top: 15px;
        }  
        &--exp::before{
            background-image: url(../img/index/Star.svg);
        } 
        &--feed::before{
            background-image: url(../img/index/like.svg);
        } 
        &--lib::before{
            background-image: url(../img/index/Layouts.svg);
        } 
        &--com::before{
            background-image: url(../img/index/Chat.svg);
        }
    }
    .mixitup-control-active{
        color: #FF3F3A;
        border: 1px solid #FF3F3A;
        border-radius: 4px;
        .benefits__btn--exp::before{
            background-image: url(../img/index/Star-stroke.svg);
        } 
    }
    &__card{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__card-content{
        max-width: 495px;
    }
    &__card-heading{
        font-weight: 900;
        font-size: 32px;
        line-height: 130%;
        color: $font-color;
        margin-bottom: 24px;
    }
    &__card-paragraph{
        color: #424551;
        font-size: 16px;
    }
}
.benefits__btn--exp.mixitup-control-active::before{
    background-image: url(../img/index/Star-stroke.svg);
}
.benefits__btn--feed.mixitup-control-active::before{
    background-image: url(../img/index/like-stroke.svg);
}
.benefits__btn--lib.mixitup-control-active::before{
    background-image: url(../img/index/Layouts-stroke.svg);
}
.benefits__btn--com.mixitup-control-active::before{
    background-image: url(../img/index/Chat-stroke.svg);
}

.lesson{
    position: relative;
    &::after{
        content: '';
        background-image: url(../img/index/circles1.svg);
        position: absolute;
        width: 225px;
        height: 284px;
        top: -142px;
        background-repeat: no-repeat;
        right: 0;
    }
    &::before{
        content: '';
        background-image: url(../img/index/lessondots.svg);
        position: absolute;
        width: 116px;
        height: 159px;
        bottom: 10px;
        background-repeat: no-repeat;
        left: 10px
    }
    padding: 80px 0;
    margin-bottom: 120px;
    background: linear-gradient(55.95deg,rgba(255,63,58,.2) 0,rgba(247,94,5,.2) 100%);
    &__subtitle{
        @extend %font;
        letter-spacing: 1px;
        text-transform: uppercase;
    }
    &__heading{
        text-align: center;
    }
    &__title{
        font-weight: 900;
        font-size: 46px;
        line-height: 130%;
        color: $font-color;
        margin-bottom: 60px;
    }
    &__events{
        margin-bottom: 60px;
    }
    &__event{
        align-items: center;
        position: relative;
        cursor: pointer;
        &:hover{
            .lesson__event-btn{
                background: linear-gradient(55.95deg, #FF3F3A 0%, #F75E05 100%);
                color: #fff;
            }
            box-shadow: 0px 80px 80px -20px rgba(255, 63, 58, 0.16), 0px 30px 24px -10px rgba(255, 63, 58, 0.1), 0px 12px 10px -6px rgba(255, 63, 58, 0.08), 0px 4px 4px -4px rgba(30, 33, 44, 0.05);
        }

        background-color: #fff;
        padding: 32px 40px;
        width: 100%;
        border-radius: 4px;
        display: flex;
        margin-bottom: 24px;
        &-num{
            font-weight: 900;
            font-size: 48px;
            line-height: 130%;
            letter-spacing: 1px;
            color: #FF3F3A;
            margin-right: 20px;
        }
        &-time{
            display: flex;
            flex-direction: column;
            margin-right: 40px;
        }
        &-date{
            @extend %font;
            margin-bottom: 4px;
            font-size: 20px;
            min-width: 98px
        }
        &-hour{
            color: #787A80;
        }
        &-info{
            max-width: 757px;
            margin-right: 156px;
        }
        &-paragraph{
            @extend %font;
            font-size: 18px;
            margin-bottom: 4px;
        }
        &-text{
            color: #787A80;  
        }
        &-btn{
            position: absolute;
            right: 40px;
            height: 44px;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 136px;
            background: inherit;
            border: 1px solid #f75e05;
            border-radius: 4px;
            background-clip: text;
            color: #f75e05;
        }
    }
    &__view{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__text{
        @extend %font;
        font-size: 28px;
        margin-right: 40px;
    }
    &__btn{
        color: white;
        height: 52px;
        padding: 0 40px;
    }
}

.certificate{
    min-height: 400px;
    &__inner{
        display: flex;
        justify-content: space-between;
    } 
    &__content{
        max-width: 392px;
    }
    &__preheading{
        @extend %font;
        letter-spacing: 1px;
        margin-bottom: 8px;
    }
    &__heading{
        @extend %bigfont;
        font-size: 46px;
        margin-bottom: 48px;
    }
    &__text{
        margin-bottom: 32px;
    }
    &__logos{
        display: flex;
        justify-content: space-between;
    }
    margin-bottom: -45px;
}

.our-team{
    background: #F4F5F7;
    padding-top: 149px;
    padding-bottom: 150px;
    &__content{
       margin-bottom: 60px;
    }
    &__title{
        letter-spacing: 1px;
        @extend %font;
    }
    &__text{
        @extend %bigfont;
        font-size: 46px;
    }
    &__cards{
        display: flex;
    }
    &__card-img{
        border-radius: 7px;
        
    }
    &__card-content{
        max-width: 95%;
        margin-bottom: 16px;
        position: relative;
        cursor: pointer;
        &:hover{
            .our-team__card-social{
                display: flex;
                border-radius: 7px;
            }
        }
    }
    &__card-social{
        display: none;
        position: absolute;
        width: 95%;
        height: 100%;
        bottom: 0;
        border-radius: 4px;
        background: linear-gradient(180deg, rgba(30, 33, 44, 0) 43.9%, rgba(30, 33, 44, 0.03) 55.21%, rgba(30, 33, 44, 0.33) 68.73%, rgba(30, 33, 44, 0.56) 76.17%, rgba(30, 33, 44, 0.768225) 83.75%, rgba(30, 33, 44, 0.92) 91.49%, #1E212C 100%);
    }
    &__card-inner{
        display: flex;
        position: absolute;
        bottom: 20px;
        right: 20px;
        align-items: flex-end;
        width: 84px;
        justify-content: space-between;
    }
    &__card-heading{
        text-align: center;
        color: $heading-color;
        @extend %font;
        font-size: 20px;
        margin-bottom: 4px;
   }
   &__card-text{
       text-align: center;
       color: #787A80;
   }
}
.our-team__card-fac{
     background-image: url(../img/index/our-team-fac--active.svg);
    width: 10px;
     height: 18px;
    opacity: 0.5;
    &:hover{
        opacity: 1;
    }
}

.our-team__card-inst{
    background-image: url(../img/index/our-team-inst--active.svg);
    width: 17px;
    height: 17px;
    opacity: 0.5;
    &:hover{
     opacity: 1;
    }
 }
 .our-team__card-link{
    background-image: url(../img/index/our-team-link--active.svg);
    width: 17px;
    height: 17px;
    opacity: 0.5;
    &:hover{
     opacity: 1;
    }
}

.subscribe{
    position: relative;
    &::before, &::after{
        content: '';
        background-image: url(../img/illustration__subscribe.png);
        position: absolute;
        width: 354px;
        height: 246px;
        bottom: 0;
    }
    &::before{
        left: 0;
    }
    &::after{
        right: 0;
    }
    display: flex;
    flex-direction: column;
    padding: 120px 0 180px;
    background: linear-gradient(55.95deg, rgba(255, 63, 58, 0.2) 0%, rgba(247, 94, 5, 0.2) 100%);
    align-items: center;
    
    &__content{
        z-index: 2;
    }
    &__preheading{
        @extend %font;
        letter-spacing: 1px;
        text-transform: uppercase;
        margin-bottom: 8px;
        text-align: center;
    }
    &__heading{
        @extend %bigfont;
        margin-bottom: 60px;
        font-size: 46px;
        text-align: center;
    }
    &__form{
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 2;
    }
    &__btn{
        height: 52px;
        width: 154px;
        font-weight: 700;
        font-size: 16px;
        line-height: 52px;
        letter-spacing: 0.5px;
    }
    &__input{
        height: 52px;
        border: 1px solid #D7DADD;
        border-radius: 4px; 
        width: 422px;
        margin-right: 24px;
        color: #9A9CA5;
        padding: 13px 16px;
        &::placeholder{
            color: #9A9CA5;
        }
    }
}




