html,
body {
	height: 100%;
  width: 100%;
  overflow-x: hidden;
  color: #424551;
  font-family: 'lato', serif;
  font-size: 16px;
  line-height: 160%;
}

a{
  color: inherit;
  cursor: pointer;
}

.container{
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn{
  background: linear-gradient(55.95deg, #FF3F3A 0%, #F75E05 100%);
  border-radius: 4px;
  border-color: transparent;
  letter-spacing: 0.5px;
  font-weight: 700;
  color: #fff;
}







