@import 'vars';
@import 'fonts';
@import 'reset';
@import 'libs';
@import 'global';
@import 'index';



.mobile-nav.active{
    display: flex;
    align-items: center;
    padding-top: 50px;
}
.mobile-nav{
display: none; 
position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;
background: rgba(#fff, .9);
z-index: 99;
flex-direction: column;
align-items: flex-start;
padding: 30px 60px 0;
transition: display 3s;
}

.mobile-nav-button{
    display: none;
    cursor: pointer;
	padding: 0;
	border: none;
	background: transparent;
	position: relative;
	z-index: 100;
	width: 40px;
	height: 30px;
	justify-content: center;
	align-items: center;
}
.mobile-nav-button__icon {
	display: block;
	position: relative;
	width: 40px;
	height: 4px;
	background-color:#424551;
}

.mobile-nav-button__icon::after,
.mobile-nav-button__icon::before {
	position: absolute;
	content: '';
	width: 40px;
	height: 4px;
	background-color: #424551;
    /*    */
	transition: transform 0.15s ease-in, top 0.15s ease-in 0.15s;
}

.mobile-nav-button__icon::before {
	left: 0;
	top: -10px;
}

.mobile-nav-button__icon::after {
	left: 0;
	top: 10px;
}

.mobile-nav-button__icon.active {
	background-color: transparent;
}

.mobile-nav-button__icon.active::before {
	transform: rotate(45deg);
	top: 0;
    /*    */
	transition: top 0.15s linear, transform 0.15s ease-in 0.15s;
}

.mobile-nav-button__icon.active::after {
	transform: rotate(-45deg);
	top: 0;
    /*    */
	transition: top 0.15s linear, transform 0.15s ease-in 0.15s;
}
.button__icon::before {
    position: absolute;
    content: '';
    width: 40px;
    height: 5px;
    background-color: #424551;
    transition: transform 0.15s ease-in, top 0.15s ease-in 0.15s;
}

.header{
    font-weight: bold;
    &__inner{
        display: flex;
        padding-top: 20px;
        justify-content: space-between;
        align-items: center;
    }
    // &__logo{
    //     margin-right: 60px;
    // }
    &__login{
        display: flex;
    }
    &__login-links{
        display: flex;
        align-items: center;
        z-index: 10;
    }
    &__login-link--login{
        position: relative;
        &::before{
            content: url('../img/login.png');
            position: absolute;
            left: -27px;
            top: 4px;
            width: 17px;
            height: 17px;
        }
    }
}

.menu{
   &__list{
    display: flex;
    justify-content: space-between;
    max-width: 484px;
   } 
   &__list-item +  &__list-item{
