   }
}

.big__btn{
    padding: 10px 40px;
    color: #FFFFFF;
    margin-right: 63px;
}

.footer{
    &__inner{
        background: #1E212C;
    }
    &__content{
        display: flex;
        justify-content: space-between;
        padding: 60px 0 80px;
    }
    &__info{
        max-width: 280px;
    }
    &__sign{
        max-width: 286px;
    }
    &__social{
        display: flex;
    }
    &__logo{
        margin-bottom: 24px;
    }
    &__text{
        margin-bottom: 38px;
        opacity: 0.6;
        color: #fff;
        font-weight: 400;
        font-size: 12px;
        line-height: 150%;
    }
    &__social-link + &__social-link{
        margin-left: 20px;
    }
    &__nav-heading, &__contacts-heading, &__sign-heading, &__contacts-heading{
        color: #fff;
        margin-bottom: 12px;
    }
    &__nav-link{
        font-weight: 400;
        font-size: 16px;
        line-height: 160%;
        opacity: 0.6;
        color: #fff;
        margin-bottom: 8px;
    }
    &__contacts{
        display: flex;
        flex-direction: column;
        color: #fff;
    }
    &__contacts-phone{
        margin-bottom: 8px;
        padding-left: 24px;
        position: relative;
        opacity: 0.6;
        &::before{
            content: url('../img/iPhone.svg');
            position: absolute;
            width: 10px;
            height: 15px;
            left: 0;
            top: 2px;
        }
    }
    &__contacts-mail{
        opacity: 0.6;
        padding-left: 24px;
        position: relative;
        &::before{
            content: url('../img/Mail (1).svg');
            position: absolute;
            width: 10px;
            height: 15px;
            left: 0;
            top: 2px;
        }
    }
    &__sign-form{
       position: relative;
       margin-bottom: 12px;
    }
    &__sign-input{
        background: rgba(255, 255, 255, 0.12);
        border: 1px solid rgba(255, 255, 255, 0.2);
        box-sizing: border-box;
        border-radius: 4px;
        width: 100%;
        padding: 9px 12px;
        color: rgba(#fff, 0.6);
    }
    &__sign-button{
        border: none;
        background-color: transparent;
        position: absolute;
        top: 20px;
        right: 12px;
        cursor: pointer;
    }
    &__sign-paragraph{
        font-weight: 400;
        font-size: 10px;
        line-height: 150%;
        color: #fff;
    }

    &__copy{
        background: #292C37;
        padding: 19px 0;
    }
    &__copy-content{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__copy-information{
        font-size: 12px;
        line-height: 150%;
        display: flex;
        color: #fff;
    }
    &__copy-text{
        position: relative;
        margin-right: 10px;
        padding-right: 18px;
        &::after{
            content: url(../img/Heart.svg);
            position: absolute;
            top: 2.5px;
            right: -4px;
        }
    }
    &__copy-top{
        font-size: 14px;
        font-weight: 700;
    }
}

.sign-up{
    &--close{
        display: none;
    }
    z-index: 999;
    width: 100%;
    background-color: rgba(#1E212C, .75); 
    display: flex;
    justify-content: center;
    padding-top: 20px;
    height: 100%;
    min-height: 1100px;
    position: absolute;
    &__content{
        max-width: 486px;
        height: 910px;
        background-color: #fff;
        z-index: 10;
    }
    &__cross{
        margin: 28px 0 15px 438px ;
        position: relative;
        cursor: pointer;
    }
    &__heading{
        font-weight: 700;
        font-size: 28px;
        line-height: 150%;
        color: $font-color;
        display: flex;
        justify-content: center;
        margin-bottom: 24px;
    }
    &__text{
        margin: 0 48px 24px;
        line-height: 150%;
        text-align: center;
        color: #787A80;
    }
    &__form{
        margin: 0 48px; 
        display: flex;
        flex-direction: column;
        position: relative;
    }
    &__email-heading, &__password-heading{
        color: #424551;
        line-height: 150%;
        margin-bottom: 8px;
    }
    &__email-input, &__password-input{
        margin-bottom: 20px;
        padding: 12px 16px;
        border: 1px solid #D7DADD;
        border-radius: 4px;
    }
   &__eye{
       width: 16px;
       height: 16px;
       position: absolute;
       right: 16px;
       margin-left: 15px;
       &--1{
           top: 258px;
       }
       &--2{
        top: 362px;
    }
   }
   &__check{
       margin: 0 48px 20px;
       display: flex;
       align-items: center;
       justify-content: space-between;
   }
   &__checkbox{
       display: flex;
       align-items: center;
   }
   &__check-flag{
       width: 16px;
       height: 16px;
       margin-right: 15px;
   }
   &__forget-password{
       color: #FF3F3A;
   }
   &__btn{
       width: 390px;
       max-width: 390px;
       margin: 0 48px 25px;
       height: 44px;
       color: #fff;
   }
   &__no-account{
       position: relative;
       padding-bottom: 28px;
       margin-bottom: 28px;
       &::before{
        content: '';
        position: absolute;
        height: 1px;
        width: 100%;
        background-color: #E5E8ED;
        bottom: 0;
       }
   }
   &__dont-have{
    margin-left: 48px;
   }
   &__sign-up{
    color: #FF3F3A;
   }
   &__social{
       display: flex;
       align-items: center;
       flex-direction: column;
   }

   &__facebook, &__google, &__twitter, &__linked{
       width: 20px;
       height: 20px;
       background-repeat: no-repeat;
       object-fit: cover;
       color: rgba(#fff, 0);
   }

   &__facebook, &__google, &__twitter{
       margin-right: 10px;
   }
   &
   &__google{
    background-image: url(../img/sign-in__google.svg);
    &:hover{
        background-image: url(../img/sign-in__google__active.svg);
    } 
    }
    &__facebook{
        background-image: url(../img/sign-in__facebook.svg);
        &:hover{
            background-image: url(../img/sign-in__facebook__active.svg);
        } 
    }
    &__twitter{
        background-image: url(../img/sign-in__twitter.svg);
        &:hover{
            background-image: url(../img/sign-in__twitter__active.svg);
        } 
    }
    &__linked{
        background-image: url(../img/sign-in__linked.svg);
        &:hover{
            background-image: url(../img/sign-in__linked__active.svg);
        } 
    }
    &__social-link{
        margin-bottom: 20px;
    }
}

