.slider-left{
    background-image: url(../img/slider__left.svg);
    width: 24px;
    height: 24px;
    top: -82.5px;
    right: 63px;
}
.slider-right{
    background-image: url(../img/slider__right.svg);
    width: 24px;
    height: 24px;
    right: 0;
    top: -82.5px;
}
.slider-left--active{
    background-image: url(../img/slider-left--active.svg);
    width: 48px;
    height: 48px;
    top: -96px;
}
.slider-right--active{
    background-image: url(../img/slider-right--active.svg);
    width: 48px;
    height: 48px;
    cursor: pointer;
    right: 0;
    top: -96px;
}

.slider-right--active, .slider-left--active, .slider-right, .slider-left{
    background-color: inherit;
    border: none;
    position: absolute;
    cursor: pointer;
}

